import React from 'react'
import {H2} from "./styles"
import good from "./good.png"
import styled from "styled-components"

const Div = styled.div`
    width:max-content;
    height:max-content;
    margin:15px auto;
`

class Result2 extends React.Component{

    constructor(props){
        super(props)
    }

    render(){
        return <div>
            <H2>당신의 성격유형은 {this.props.mbti} 입니다.</H2>
            <Div><img src={good} style={ { width:"300px", height:"300px"} }></img></Div>
            <H2>나와 같은 {this.props.mbti} 유형을 갖고 있는 사람 : {this.props.who}</H2>
        </div>
    }
}

export default Result2